export default "/**\n *\n * Hey. My name's Ronny Coste. I'm a Software Engineer, Photographer, Creative, Tinkerer and \n * javascript + Frameworks enthusiast.\n *\n * I build interactive and responsive websites.\n * Like this one... and others ie. My cinema and photography portfolio..\n *\n * Fancy some live coding??\n * Well let's do it!\n */\n\n/**\n * Let's start. I will start by animating...\n * Forgot to say... We are animating everything ;)\n */\n\n * {\n  -webkit-transition: all 2s; /* Speed of the Transitions */\n}\n\n/**\n * That did not do much. But you'll see.\n *\n * Black on white is really boring,\n * so let's do something about it.\n */\n\nhtml {\n  background: rgb(247, 191, 190); /* Background color */\n}\n\n/***\n * Hold on...\n */\n\npre, a {\n  color: white;\n}\n\n/**\n * That's better. I'm Sorry for your eyes. Forgive me i am still learning.\n *\n * Working in this big empty canvas is tough.\n *\n * I'm going to make a nice area work area for us.\n */\n\npre:not(:empty) {\n  /* Box with the code */\n  overflow: auto;\n  background: rgb(48, 48, 48);\n  border: 1px solid #ccc;\n  max-height: 45vh;\n  width: 49%;\n  font-size: 14px;\n  font-family: monospace;\n  padding: 1vh 0.5vw;\n  box-shadow: -4px 4px 2px 0 rgba(0,0,0,0.3);\n  white-space: pre-wrap;\n  outline: 0;\n  margin: 1vh 0.5vw;\n} \n\n/**\n * Okay. Now We're going to start filling up the screen.\n * Let's get our hands dirty!\n * Umm... Right this is automated. I forgot.\n */\n\n#style-text {\n  -webkit-transform: translateX(90%);\n  position: absolute;\n}\n\n/**\n * This is looking good, but it could be much better!\n * Lets change all this white text.\n * and let's make it even more readable.\n */\n\n.comment       { color: #ABDAD9; font-style: italic; } \n.selector      { color: #E69F0F; } \n.selector .key { color: #64D5EA; }\n.key           { color: #64D5EA; }\n.value         { color: #BE84F2; }\n.value.px      { color: #F92772; }\n\n/**\n * Now we're getting somewhere.\n * Time to give this a little perspective.\n */\n\nbody {\n  -webkit-perspective: 1500px; /* Inwards perspective */\n}\n\n#style-text {\n  -webkit-transform: translateX(98.5%) rotateY(-10deg);\n  -webkit-transform-origin: right; /* Resume Box to the right */\n  max-height: 93.1vh;\n}\n\n/**\n * So, let's get to the point of what you are here for. Let's talk projects.\n * I can't imagine you'd come just to see some cool looking animations and pretty colors.\n */\n\npre:not(#style-text) {\n  -webkit-transform: rotateY(10deg); /* Degree of the boxes. Changes this would break the consitency of the three boxes. */\n  -webkit-transform-origin: left; /* Position the code box on the left */\n}";